import React from 'react'
import styles from './button.module.scss'
import cx from 'classnames'
import { COLOR_THEME, ButtonType } from './constants'
import closeIcon from './assets/img/close.svg'

type Props = {
	text: string
	active?: boolean
	type?: ButtonType
	disabled?: boolean
	colorTheme?: string
	withBorder?: boolean
	deleteButton?: boolean
	onClick: () => void
	onDelete?: (e: React.FormEvent) => void
}

export const Button: React.FC<Props> = ({
	text,
	type,
	active,
	disabled,
	colorTheme,
	withBorder,
	deleteButton,
	onClick,
	onDelete,
}) => {
	return (
		<button
			className={cx(styles.button, {
				[styles.active]: active,
				[styles.disabled]: disabled,
				[styles.withBorder]: withBorder,
				[styles.redTheme]: colorTheme === COLOR_THEME.RED_THEME,
				[styles.blueTheme]: colorTheme === COLOR_THEME.BLUE_THEME,
			})}
			disabled={disabled}
			type={type || 'submit'}
			onClick={onClick}
		>
			{text}
			{deleteButton && (
				<div className={styles.delete} onClick={onDelete}>
					<img src={closeIcon} alt='close icon' />
				</div>
			)}
		</button>
	)
}
