import React from 'react'
import { Image, Transformer } from 'react-konva'
import 'gifler'
import { observer } from 'mobx-react'
import { Gif } from '../../store'
import Konva from 'konva'

interface Props {
	gif: Gif
	isSelected: boolean
	onSelect: () => void
	onChange: (newAttrs: Gif) => void
}

const GifComponent: React.FC<Props> = ({ gif, isSelected, onChange, onSelect }) => {
	const gifRef = React.useRef<any>(null)
	const trRef = React.useRef<any>()

	const canvas = React.useMemo(() => {
		const node = document.createElement('canvas')
		return node
	}, [])

	React.useEffect(() => {
		let anim: any

		// @ts-ignore
		window.gifler(gif.src).get((a: any) => {
			anim = a
			anim.animateInCanvas(canvas)
			anim.onDrawFrame = (
				ctx: { drawImage: (arg0: any, arg1: any, arg2: any) => void },
				frame: { buffer: any; x: any; y: any }
			) => {
				ctx.drawImage(frame.buffer, frame.x, frame.y)
				if (!gifRef.current) return
				// @ts-ignore
				gifRef.current.getLayer().draw()
			}
		})
		return () => anim.stop()
	}, [gif.src, canvas])

	React.useEffect(() => {
		if (isSelected && trRef.current) {
			// we need to attach transformer manually
			trRef.current.nodes([gifRef.current])
			trRef.current.getLayer().batchDraw()
		}
	}, [isSelected])

	const onDragEnd = (e: Konva.KonvaEventObject<DragEvent>) => {
		onChange({ ...gif, x: e.target.x(), y: e.target.y() })
	}

	const onTransformEnd = (e: Konva.KonvaEventObject<Event>) => {
		const node = gifRef.current
		const scaleX = node.scaleX()
		const scaleY = node.scaleY()

		node.scaleX(1)
		node.scaleY(1)
		onChange({
			...gif,
			x: node.x(),
			y: node.y(),
			width: Math.max(5, node.width() * scaleX),
			height: Math.max(node.height() * scaleY),
		})
	}

	return (
		<>
			<Image
				ref={gifRef}
				image={canvas}
				x={gif.x}
				y={gif.y}
				width={gif.width}
				height={gif.height}
				draggable
				onClick={onSelect}
				onTap={onSelect}
				onDragEnd={onDragEnd}
				onTransformEnd={onTransformEnd}
			/>
			{isSelected && (
				<Transformer
					ref={trRef}
					boundBoxFunc={(oldBox, newBox) => {
						if (newBox.width < 5 || newBox.height < 5) {
							return oldBox
						}
						return newBox
					}}
				/>
			)}
		</>
	)
}

export default observer(GifComponent)
