import React, { ChangeEvent, useEffect, useRef, WheelEvent } from 'react'

import { TextField } from '@material-ui/core'
import styles from './customTimeInput.module.scss'
import cx from 'classnames'

import { formatTime } from '../../utils/format-time'
import arrowIcon from './assets/img/arrow.svg'

export const POSITION = {
	left: 'left',
	right: 'right',
}

export type Position = typeof POSITION.left | typeof POSITION.right

interface Props {
	value: number
	label?: string
	position: Position
	onChange: (value: number) => void
}

export const CustomTimeInput: React.FC<Props> = ({ position, value, onChange }) => {
	const textFieldRef = useRef(null)

	const onChangeHandler = (e: ChangeEvent<HTMLInputElement>) => {}

	const onWheel = (e: WheelEvent<HTMLElement>): void => {
		e.preventDefault()

		if (e.deltaY > 0) {
			onAdd()
		} else {
			onRemove()
		}
	}

	const onRemove = () => {
		if (!value) return

		let newVal = value - 0.1

		if (newVal < 0) newVal = 0

		onChange(newVal)
	}

	const onAdd = () => {
		onChange(value + 0.1)
	}

	useEffect(() => {
		const textField = textFieldRef.current

		;(textField as any)?.addEventListener('wheel', onWheel)
		return () => (textField as any)?.removeEventListener('wheel', onWheel)
	}, [value])

	const formattedProps = {
		value: formatTime(value, true),
	}

	return (
		<div className={styles.container}>
			<TextField
				{...formattedProps}
				className={styles.timeInput}
				size={'small'}
				helperText={null}
				variant='outlined'
				onChange={onChangeHandler}
				ref={textFieldRef}
				InputProps={{
					readOnly: true,
				}}
			/>
			<div
				className={cx(styles.buttons, {
					[styles.leftPosition]: position === POSITION.left,
					[styles.rightPosition]: position === POSITION.right,
				})}
			>
				<div className={styles.buttonsWrapper}>
					<button onClick={onAdd} className={styles.addButton}>
						<img src={arrowIcon} alt='Arrow icon' />
					</button>
					<button onClick={onRemove} className={styles.removeButton}>
						<img src={arrowIcon} alt='Arrow icon' />
					</button>
				</div>
			</div>
		</div>
	)
}
