import { when } from 'mobx'
import ReactPlayer from 'react-player'
import store, { Mode } from '../store'

interface PlayerControl {
	player: ReactPlayer
}

class PlayerService {
	control: PlayerControl[] = []

	setPlayer(playerIndex: number, player: PlayerControl) {
		this.control[playerIndex] = player
	}

	async stop(playerIndex: number) {
		if (!store.playing[playerIndex]) return
		store.playing[playerIndex] = false
		await when(() => !store.players[playerIndex].playing)
	}

	async start(playerIndex: number) {
		if (store.playing[playerIndex]) return
		store.playing[playerIndex] = true
		await when(() => store.players[playerIndex].playing)
	}

	async playOne(cutIndex: number) {
		store.mode = Mode.PlayOne

		await this.stop(store.activePlayerIndex)
		store.currentCutIndex = cutIndex
		await this.preloadCut(store.activePlayerIndex, cutIndex)
		await this.start(store.activePlayerIndex)
	}

	async playAll(isShowMode?: boolean) {
		store.isLoading = true

		if (store.cuts.length === 0) {
			// this.changeModeToCreate(0);
			await this.start(store.activePlayerIndex)
			return
		}

		if (!isShowMode) store.mode = Mode.PlayAll
		await this.setDefaultPlayersState()
		await this.start(store.activePlayerIndex)
	}

	async changeModeToCreate(sourceIndex: number) {
		store.mode = Mode.Create

		store.currentSourceIndex = sourceIndex
		store.currentCutIndex = -1

		for (let i = 0; i < store.players.length; i += 1) {
			await this.stop(i)

			if (i === store.activePlayerIndex) {
				store.players[i] = {
					...store.players[i],
					url: store.sources[sourceIndex].url,
					playing: false,
				}
			}
		}
	}

	async changeModeToShow() {
		store.mode = Mode.Show
		await this.playAll(true)
	}

	async preloadCut(playerIndex: number, cutIndex: number) {
		store.players[playerIndex] = {
			...store.players[playerIndex],
			url: store.cuts[cutIndex].url,
			cutIndex,
		}

		store.players[playerIndex].preloading = true
		store.players[playerIndex].muted = true

		await this.start(playerIndex)
		await this.stop(playerIndex)
		this.control[playerIndex].player.seekTo(store.cuts[cutIndex].cut.startTime)

		store.players[playerIndex].muted = false
		store.players[playerIndex].preloading = false
	}

	async setDefaultPlayersState() {
		for (let i = 0; i < store.players.length; i += 1) {
			await this.stop(i)
			if (i < store.cuts.length) {
				store.players[i].isVisible = i === 0
				await this.preloadCut(i, i)
			} else {
				store.players[i] = {
					id: store.players[i].id,
					muted: false,
					url: '',
					playing: false,
					preloading: false,
					cutIndex: i,
					isVisible: false,
				}
			}
		}

		store.activePlayerIndex = 0
		store.currentCutIndex = 0
	}

	async goToNextCut(currentPlayerIndex: number) {
		const isLastCut = store.currentCutIndex === store.cuts.length - 1

		store.isShowTransition = !isLastCut
		await this.stop(currentPlayerIndex)

		if (isLastCut) {
			console.log('Last cut is end')
			return
		}

		const cutIndexForPreload = store.currentCutIndex + store.players.length
		const isNeedToPreloadCut = cutIndexForPreload < store.cuts.length

		console.log(`Go from ${store.currentCutIndex} to ${store.nextCutIndex} cut`)

		store.activePlayerIndex = store.nextPlayerIndex
		store.currentCutIndex = store.nextCutIndex

		await this.start(store.activePlayerIndex)
		store.players[currentPlayerIndex].isVisible = false
		store.players[store.activePlayerIndex].isVisible = true

		setTimeout(() => (store.isShowTransition = false), 400)

		if (!isNeedToPreloadCut) return

		await this.preloadCut(currentPlayerIndex, cutIndexForPreload)
	}

	getTimePosition() {
		const currentTime = this.control[store.activePlayerIndex].player.getCurrentTime()

		if (store.currentCutIndex < 0) return currentTime

		let timePosition = 0

		for (let i = 0; i < store.cuts.length; i += 1) {
			if (i > store.currentCutIndex) break
			const startTime = store.cuts[i].cut.startTime
			const endTime = store.cuts[i].cut.endTime

			if (i === store.currentCutIndex) {
				timePosition += currentTime - startTime
			} else {
				timePosition += endTime - startTime
			}
		}

		return timePosition
	}
}

const playerService = new PlayerService()

export default playerService
