import React from 'react'
import { observer } from 'mobx-react'
import CopyToClipboard from 'react-copy-to-clipboard'
import { useState } from 'react'
import { Button, Grid, CircularProgress } from '@material-ui/core'

import store, { Cut } from '../../store'
import styles from './CutsPanel.module.scss'
import CutItem from '../CutItem'
import { formatTime } from '../../utils/format-time'
import CutPlayer from '../CutPlayer/CutPlayer'
import playerService from '../../services/playerService'

function CutsPanel() {
	const [showCopied, setShowCopied] = useState(false)

	const onClickHandler = async (idx: number) => {
		if (store.activeCut === idx) {
			store.activeCut = null
			return
		}
		store.activeCut = idx
		await playerService.preloadCut(store.activePlayerIndex, idx)
	}

	async function playAll() {
		await playerService.playAll()
	}

	function onUrlCopy() {
		setShowCopied(true)
		setTimeout(() => setShowCopied(false), 3000)
		gtag('event', 'url-copy', {
			url: store.shareUrl,
		})
	}

	const clearStore = () => store.clearStore()

	return (
		<div className={styles.cutsPanel}>
			{store.sources.length > 0 ? (
				<>
					<div className={styles.cutsList}>
						{store.cuts.map((item, idx) => (
							<CutItem
								key={item.id}
								idx={idx}
								cut={item.cut as Cut}
								isActive={idx === store.activeCut}
								onClick={onClickHandler}
							/>
						))}
					</div>
					{store.activeCut !== null && <CutPlayer idx={store.activeCut} />}
					<Grid container className={styles.infoLine} item>
						<Grid item lg>
							Trailer duration: {formatTime(store.trailerLength)}
						</Grid>
						<Grid item lg>
							Number of frames: {store.cuts.length}
						</Grid>
					</Grid>
					<Grid className={styles.bottomBtn}>
						<Button
							variant='contained'
							disableElevation
							onClick={playAll}
							fullWidth
							color='primary'
							disabled={store.isLoading}
							style={{ margin: 5 }}
						>
							Play all
							{store.isLoading && <CircularProgress size={20} />}
						</Button>
						<CopyToClipboard text={store.shareUrl || ''} onCopy={onUrlCopy}>
							<Button variant='contained' disableElevation fullWidth style={{ margin: 5 }}>
								{showCopied ? 'Copied' : 'Copy link'}
							</Button>
						</CopyToClipboard>
						<Button
							onClick={clearStore}
							variant='contained'
							disableElevation
							fullWidth
							style={{ margin: 5 }}
						>
							Clear store
						</Button>
					</Grid>
				</>
			) : (
				<Grid container className={styles.cutsPanelBlank}>
					Please select video for trailer editing
				</Grid>
			)}
		</div>
	)
}

export default observer(CutsPanel)
