import React from 'react'
import ReactPlayer from 'react-player'
import { observer } from 'mobx-react'

import store, { Cut, Mode } from '../../store'
import styles from './MultiPlayer.module.sass'
import playerService from '../../services/playerService'

interface Props {
	updatePosition: (playedSeconds: number) => void
}

function MultiPlayer(props: Props) {
	const { updatePosition } = props
	// const playerRef = React.createRef<ReactPlayer>()

	const onReady = (playerIndex: number, player: ReactPlayer) => {
		console.log(`Player ${playerIndex} onReady`)

		playerService.setPlayer(playerIndex, {
			player,
		})
	}

	const onProgress = async (playerIndex: number, playerState: any) => {
		if (
			playerIndex !== store.activePlayerIndex ||
			store.players[playerIndex].preloading ||
			!store.players[playerIndex].playing
		) {
			console.log(`Player ${playerIndex} onProgress stopped`)
			return
		}

		if (store.isLoading) store.isLoading = false
		updatePosition(playerService.getTimePosition())

		const cutIndex = store.players[playerIndex].cutIndex
		if (cutIndex === undefined || store.currentCutIndex === -1) {
			console.log(`Player ${playerIndex} onProgress No cut index in player`)
			return
		}
		const cut = store.cuts[cutIndex].cut
		console.log(
			`Player ${playerIndex} onProgress in ${playerState.playedSeconds} sec (s: ${cut?.startTime} e: ${cut?.endTime}`
		)

		switch (store.mode) {
			case Mode.Create:
				break
			case Mode.PlayOne:
				if (isInCut(cut, playerState.playedSeconds)) return
				await playerService.stop(playerIndex)
				break
			case Mode.PlayAll:
				if (isInCut(cut, playerState.playedSeconds)) return
				await playerService.goToNextCut(playerIndex)
				break
			case Mode.Show:
				if (isInCut(cut, playerState.playedSeconds)) return
				await playerService.goToNextCut(playerIndex)
				break
		}
	}

	const onBuffer = (playerIndex: number) => {
		console.log(`Player ${playerIndex} onBuffer`)
	}

	const onBufferEnd = (playerIndex: number) => {
		console.log(`Player ${playerIndex} onBufferEnd`)
	}

	const onStart = (playerIndex: number) => {
		console.log(`Player ${playerIndex} onStart`)
		// if (!store.playing[playerIndex]) store.playing[playerIndex] = true
		// store.players[playerIndex].playing = true
	}

	const onPlay = (playerIndex: number) => {
		console.log(`Player ${playerIndex} onPlay`)
		if (!store.playing[playerIndex]) store.playing[playerIndex] = true
		store.players[playerIndex].playing = true
	}

	const onPause = (playerIndex: number) => {
		console.log(`Player ${playerIndex} onPause`)
		if (store.playing[playerIndex]) store.playing[playerIndex] = false
		store.players[playerIndex].playing = false
	}

	const onEnded = (playerIndex: number) => {
		console.log(`Player ${playerIndex} onEnded`)
	}

	const onSeek = (playerIndex: number, e: any) => {
		console.log(`Player ${playerIndex} onSeek`)
	}

	const isInCut = (cut: Cut | null | undefined, playedSeconds: number) => {
		if (!cut) return false
		if (playedSeconds >= cut.startTime && playedSeconds <= cut.endTime) return true
		return false
	}

	return (
		<div>
			{store.sources.length > 0 ? (
				store.players.map((item, index, array) => {
					const curIndex = array.length - 1 - index

					return (
						<div
							key={store.players[curIndex].id}
							style={{ visibility: store.players[curIndex].isVisible ? 'visible' : 'hidden' }}
						>
							<ReactPlayer
								// ref={playerRef}
								playing={store.playing[curIndex]}
								className={styles.player}
								url={store.players[curIndex].url}
								muted={store.players[curIndex].muted}
								width='100%'
								height='100%'
								onPlay={onPlay.bind(null, curIndex)}
								onProgress={onProgress.bind(null, curIndex)}
								onStart={onStart.bind(null, curIndex)}
								onPause={onPause.bind(null, curIndex)}
								onEnded={onEnded.bind(null, curIndex)}
								onReady={onReady.bind(null, curIndex)}
								onSeek={onSeek.bind(null, curIndex)}
								onBufferEnd={onBufferEnd.bind(null, curIndex)}
								onBuffer={onBuffer.bind(null, curIndex)}
								progressInterval={100}
								controls={true}
								config={{
									youtube: {
										playerVars: {},
									},
									// twitch: {
									// 	options: {
									// 		autoplay: true,
									// 		time: '0h0m0s',
									// 	},
									// },
								}}
							/>
						</div>
					)
				})
			) : (
				<div className={styles.playerPlaceholder} />
			)}
		</div>
	)
}

export default observer(MultiPlayer)
