import React from 'react'
import { observer } from 'mobx-react'
import store from '../../store'
import styles from './cutPlayer.module.scss'
import { CustomTimeInput, POSITION } from '../CustomTimeInput'
import { formatTime } from '../../utils/format-time'

type Props = {
	idx: number
}

const CutPlayer: React.FC<Props> = ({ idx }) => {
	const cut = store.cuts[idx]?.cut

	const setStart = (time: number) => {
		cut.startTime = time
		if (cut.endTime < cut.startTime + 1) cut.endTime = cut.startTime + 1

		store.cuts[idx].cut = {
			startTime: cut.startTime,
			endTime: cut.endTime,
		}
	}

	const setEnd = (time: number) => {
		cut.endTime = time
		if (cut.startTime > cut.endTime - 1) cut.startTime = cut.endTime - 1
		store.cuts[idx].cut = {
			startTime: cut.startTime,
			endTime: cut.endTime,
		}
	}
	return (
		<div className={styles.container}>
			{cut && (
				<>
					<div className={styles.timerBlock}>
						<CustomTimeInput
							value={cut.startTime}
							onChange={(time) => setStart(time)}
							position={POSITION.left}
						/>
						<p className={styles.separator}>-</p>
						<CustomTimeInput
							value={cut.endTime}
							onChange={(time) => setEnd(time)}
							position={POSITION.right}
						/>
					</div>
					<div className={styles.rightBlock}>
						<div className={styles.timeTotal}>
							Продолжительность кусочка: {formatTime(cut.endTime - cut.startTime)}
						</div>
					</div>
				</>
			)}
		</div>
	)
}

export default observer(CutPlayer)
