import React from 'react';
import { Layer, Stage } from 'react-konva';
import { observer } from 'mobx-react';
import Konva from 'konva';

import style from './CanvasLayer.module.sass';
import store, { Gif } from '../../store';
import GifComponent from './GifComponent';

interface Props {}

const CanvasLayer: React.FC<Props> = () => {
	const [selectedId, setSelectedId] = React.useState<string | null>(null);

	const checkDeselect = (e: Konva.KonvaEventObject<TouchEvent> | Konva.KonvaEventObject<MouseEvent>) => {
		const clickedOnEmpty = e.target === e.target.getStage();
		if (clickedOnEmpty) {
			setSelectedId(null);
		}
	};

	const onSelect = (gifId: string) => {
		setSelectedId(gifId);
	};

	const onChange = (gifIndex: number, newAttrs: Gif) => (store.gifs[gifIndex] = newAttrs);

	return (
		<div>
			<Stage
				className={style.container}
				width={store.canvasSize.width}
				height={store.canvasSize.height}
				style={store.isEnableCanvas ? { pointerEvents: 'all' } : { pointerEvents: 'none' }}
				onMouseDown={checkDeselect}
				onTouchStart={checkDeselect}
			>
				<Layer>
					{store.gifs.map((gif: any, i: number) => {
						return (
							<GifComponent
								key={gif.id}
								gif={gif}
								isSelected={gif.id === selectedId}
								onSelect={onSelect.bind(null, gif.id)}
								onChange={onChange.bind(null, gif.index)}
							/>
						);
					})}
				</Layer>
			</Stage>
		</div>
	);
};

export default observer(CanvasLayer);
