import React from 'react'
import { Button, CircularProgress, Grid } from '@material-ui/core'
import cx from 'classnames'
import { observer } from 'mobx-react'
import ResizeObserver from 'react-resize-observer'

import store, { Cut, Mode } from '../../store'
import { formatTime } from '../../utils/format-time'
import styles from './PlayerPanel.module.scss'
import TimeInput from '../TimeInput/TimeInput'
import Canvas from '../CanvasLayer/'
import MultiPlayer from '../MultiPlayer'
import playerService from '../../services/playerService'

export type CanvasSize = {
	width: number
	height: number
}

interface PlayerPanelState {
	cut: Cut | null
	playing: boolean
	position: number
	record: boolean
}

class PlayerPanel extends React.Component {
	state: PlayerPanelState
	private canvasRef: React.RefObject<any>

	constructor(props: any) {
		super(props)

		this.state = {
			cut: null,
			playing: false,
			position: 0,
			record: false,
		}

		this.canvasRef = React.createRef()
	}

	componentDidMount() {
		if (!this.canvasRef.current) return

		store.canvasSize = {
			width: this.canvasRef.current.offsetWidth,
			height: this.canvasRef.current.offsetHeight,
		}
	}

	changeStart = (time: number) => {
		this.setState({
			cut: {
				startTime: time,
				endTime: Math.max(this.state.cut!.endTime, time + 1),
			},
		})
	}

	changeEnd = (time: number) => {
		this.setState({
			cut: {
				startTime: Math.min(this.state.cut!.startTime, time - 1),
				endTime: time,
			},
		})
	}

	play = (): void => {
		const { cut } = this.state

		if (!cut) return

		store.players[store.activePlayerIndex].playing = false
		playerService.control[store.activePlayerIndex].player.seekTo(cut.startTime)

		setTimeout(() => {
			playerService.start(store.activePlayerIndex)

			const checkCut = () => {
				const { cut } = this.state
				if (!cut) return

				const player = playerService.control[store.activePlayerIndex].player
				const position = player ? player.getCurrentTime() : 0

				if (position >= cut?.startTime && position <= cut?.endTime) return
				clearInterval(intervalId)
				playerService.stop(store.activePlayerIndex)
			}

			const intervalId = setInterval(checkCut, 100)
		}, 1000)
	}

	clear = () => {
		this.setState({
			cut: null,
		})
	}

	onAddCut = () => {
		const { cut } = this.state

		if (!cut) return

		store.addCut(cut)
		this.setState({
			startTime: 0,
			endTime: 0,
		})
	}

	updatePosition = (playedSeconds: number) => {
		this.setState({ position: playedSeconds })
	}

	onStartFrame = () => {
		const { position, cut } = this.state
		const current = {
			startTime: cut?.startTime || 0,
			endTime: cut?.endTime || 0,
		}

		current.startTime = position
		if (current.endTime < position) current.endTime = position + 5
		this.setState({ cut: current })
	}

	onEndFrame = () => {
		const { position, cut } = this.state
		const current = {
			startTime: cut?.startTime || 0,
			endTime: cut?.endTime || 0,
		}

		current.endTime = position
		if (current.startTime > position) current.startTime = position - 5
		this.setState({ cut: current })
	}

	onResizePlayerWrapper = ({ width, height }: { width: number; height: number }) => {
		store.canvasSize = { width, height }
		store.scaleGifs()
	}

	render() {
		const { position, cut, record } = this.state

		return (
			<div className={styles.container}>
				<div style={{ fontSize: '10px' }}>
					Player: {store.activePlayerIndex}/{store.players.length - 1} Cut: {store.currentCutIndex}/
					{store.cuts.length - 1} Source: {store.currentSourceIndex}/{store.sources.length - 1}
				</div>
				<Grid ref={this.canvasRef} className={cx('padded', styles.playerWrapper)}>
					<MultiPlayer updatePosition={this.updatePosition} />
					<Canvas />
					<ResizeObserver onResize={this.onResizePlayerWrapper} />
					<div
						style={{
							position: 'absolute',
							top: 0,
							left: 0,
							backgroundColor: 'black',
							color: 'white',
							width: '100%',
							height: '100%',
							visibility: store.isShowTransition ? 'visible' : 'hidden',
						}}
					></div>
				</Grid>
				{store.mode !== Mode.Show && store.sources.length > 0 ? (
					<>
						<Grid justify='center' container>
							{formatTime(position, true)}
						</Grid>
						<Grid container>
							<button
								className={cx(styles.recButton, {
									[styles.active]: record,
								})}
								onMouseDown={() => {
									console.log('down')
									this.setState({ record: true })
									this.onStartFrame()
								}}
								onMouseUp={() => {
									console.log('up')
									this.setState({ record: false })
									this.onEndFrame()
								}}
							>
								{record ? 'stop rec' : 'start rec'}
							</button>
						</Grid>
						{cut && (
							<Grid container justify='center' className={styles.timeRow} alignContent='flex-end'>
								<Grid item lg>
									<TimeInput value={cut.startTime} label='Start time' onChange={this.changeStart} />
								</Grid>
								<Grid item lg>
									<TimeInput value={cut?.endTime} label='End time' onChange={this.changeEnd} />
								</Grid>
								<Grid
									item
									className={styles.buttons}
									xs
									justify={'flex-end'}
									alignContent={'center'}
									container
									wrap={'nowrap'}
								>
									<Button variant='outlined' disableElevation onClick={this.play}>
										Play
									</Button>
									<Button
										variant='contained'
										color='primary'
										disableElevation
										onClick={this.onAddCut}
									>
										Add
									</Button>
									<Button variant='contained' disableElevation onClick={this.clear}>
										Clear
									</Button>
								</Grid>
							</Grid>
						)}
					</>
				) : (
					<>
						<Grid className={styles.previewButtons} container>
							<Button
								variant='contained'
								size='large'
								color='primary'
								disableElevation
								disabled={store.isLoading}
								onClick={() => playerService.changeModeToShow()}
							>
								Play trailer
								{store.isLoading && <CircularProgress size={20} />}
							</Button>
							<Button
								variant='contained'
								color='primary'
								disableElevation
								onClick={() => playerService.changeModeToCreate(0)}
							>
								Make your own trailer
							</Button>
						</Grid>
					</>
				)}
			</div>
		)
	}
}

export default observer(PlayerPanel)
