import React, { useState } from 'react'
import { observer } from 'mobx-react'
import { Grid } from '@material-ui/core'

import styles from './SourcesPanel.module.scss'
import store, { Mode } from '../../store'
import { ContentView } from '../ContentView'
import { Input } from '../Input'
import { COLOR_THEME } from '../Button/constants'
import { Button } from '../Button'
import CutsPanel from '../CutsPanel'
import playerService from '../../services/playerService'

const SourcesPanel: React.FC = () => {
	const [url, setUrl] = useState('')

	const onChangeUrl = (value: string) => setUrl(value)

	const addSource = () => {
		store.addSource(url)
		setUrl('')
	}

	const changeSource = async (sourceIndex: number) => {
		await playerService.changeModeToCreate(sourceIndex)
	}

	const handleSubmit = (e: React.FormEvent) => {
		e.preventDefault()
		addSource()
	}

	const deleteSource = (e: React.FormEvent, sourceIndex: number) => {
		e.stopPropagation()
		store.removeSource(sourceIndex)

		if (sourceIndex > 0) {
			return changeSource(sourceIndex - 1)
		}

		if (sourceIndex === 0 && store.sources.length) {
			return changeSource(0)
		}

		return
	}

	return (
		<ContentView title='All frames'>
			{store.mode !== Mode.Show && (
				<Grid container direction='column'>
					<Grid className={styles.searchLine} item>
						<form onSubmit={handleSubmit} className={styles.form}>
							<div className={styles.inputWrapper}>
								<Input value={url} onChange={onChangeUrl} placeholder='Paste the video link here' />
							</div>
							<Button
								type='button'
								text='Add source'
								disabled={url === ''}
								onClick={addSource}
								colorTheme={COLOR_THEME.BLUE_THEME}
							/>
						</form>
					</Grid>
					<Grid item container spacing={2}>
						{store.sources.map((source: any) => (
							<Grid key={source.id} item>
								<Button
									text={`Source ${source.index + 1}`}
									onClick={() => changeSource(source.index)}
									onDelete={(e) => deleteSource(e, source.index)}
									deleteButton
									colorTheme={COLOR_THEME.BLUE_THEME}
									active={source.index === store.currentSourceIndex}
								/>
							</Grid>
						))}
					</Grid>
				</Grid>
			)}
			<CutsPanel />
		</ContentView>
	)
}

export default observer(SourcesPanel)
