import { observable, makeObservable, configure, computed, action, autorun } from 'mobx'
import { v4 as uuid } from 'uuid'

import parse from 'url-parse'
import { IGif } from '@giphy/js-types'
import playerService from '../services/playerService'
import { AsyncTrunk, ignore } from 'mobx-sync'

configure({ enforceActions: 'never' })

export interface Cut {
	startTime: number
	endTime: number
}

export interface Player {
	id: number
	playing: boolean
	preloading: boolean
	muted: boolean
	url: string
	cutIndex?: number
	isVisible: boolean
}

export interface CutStore {
	id: string
	index: number
	url: string
	cut: Cut
}

export interface Source {
	id: string
	index: number
	url: string
}

export enum Mode {
	Create = 'create',
	PlayOne = 'playOne',
	PlayAll = 'playAll',
	Show = 'show',
}

export interface Gif {
	id: string | number
	index: number
	src: string
	x: number
	y: number
	height: number
	width: number
	canvasHeight: number
	canvasWidth: number
}

const url = parse(window.location.href, true)
let urlStory: CutStore[] | null = null
let urlSources: Source[] | null = null

if (url.query.story) {
	localStorage.removeItem('eyeMoStore')
	window.history.pushState(null, '', '/')
	const raw = decodeURIComponent(url.query.story)
	urlStory = raw.split(',').map((player) => {
		const parts = player.split('::')

		return {
			id: parts[0],
			index: parseFloat(parts[1]),
			url: parts[2],
			cut: {
				startTime: parseFloat(parts[3]),
				endTime: parseFloat(parts[4]),
			},
		}
	})
}

if (url.query.video) {
	const raw = decodeURIComponent(url.query.video)
	urlSources = raw.split(',').map((source) => {
		const parts = source.split('::')

		return {
			id: parts[0],
			index: parseFloat(parts[1]),
			url: parts[2],
		}
	})
}

const hasUrl = urlSources && urlSources.length > 0

class Store {
	@observable sources: Source[] = urlSources || []
	@observable mode: Mode = !hasUrl ? Mode.Create : Mode.Show
	@observable cuts: CutStore[] = urlStory || []
	@observable gifs: Gif[] = []
	@observable players: Player[] = [0, 1, 2, 3].map((player, i) => {
		return {
			id: i,
			playing: false,
			preloading: false,
			muted: false,
			url: this.sources[0] ? this.sources[0].url : '',
			isVisible: i === 0,
		}
	})
	@ignore @observable playing = this.players.map((item) => false)
	@observable activePlayerIndex: number = 0
	@ignore @observable currentCutIndex: number = this.cuts.length > 0 ? 0 : -1
	@ignore @observable activeCut: number | null = null
	@ignore @observable currentSourceIndex: number = this.sources.length > 0 ? 0 : -1
	@ignore @observable isEnableCanvas: boolean = false
	@ignore @observable canvasSize: { height: number; width: number } = { height: 0, width: 0 }
	@ignore @observable isLoading: boolean = false
	@ignore @observable isShowTransition: boolean = false

	@computed get shareUrl() {
		if (this.sources.length === 0) return null
		const baseUrl = window.location.href.split('?')[0]

		return `${baseUrl}?video=${encodeURIComponent(
			this.sources
				.map((source) => {
					return [source.id, source.index, source.url].join('::')
				})
				.join(',')
		)}&story=${encodeURIComponent(
			this.cuts
				.map((player, i) => {
					return [player.id, player.index, player.url, player.cut?.startTime, player.cut?.endTime].join('::')
				})
				.join(',')
		)}`
	}

	@computed get trailerLength() {
		return this.cuts.reduce((duration, player, index) => {
			if (!player.cut) return duration
			return duration + player.cut.endTime - player.cut.startTime
		}, 0)
	}

	@computed get nextPlayerIndex() {
		if (this.activePlayerIndex === this.players.length - 1) return 0
		return this.activePlayerIndex + 1
	}

	@computed get nextCutIndex() {
		if (this.currentCutIndex === this.cuts.length - 1) return 0
		return this.currentCutIndex + 1
	}

	@action
	addCut(cut: Cut) {
		this.cuts.push({
			id: uuid(),
			index: this.cuts.length,
			url: this.sources[this.currentSourceIndex].url,
			cut: {
				startTime: cut.startTime,
				endTime: cut.endTime,
			},
		})
	}

	@action
	addSource(url: string) {
		this.sources = [
			...this.sources,
			{
				id: uuid(),
				index: this.sources.length,
				url: url,
			},
		]

		playerService.changeModeToCreate(this.sources.length - 1)
	}

	@action
	addGif(gif: IGif) {
		this.gifs = [
			...this.gifs,
			{
				id: uuid(),
				index: this.gifs.length,
				src: gif.images.original.url,
				x: 0,
				y: 0,
				height: this.canvasSize.height * 0.2,
				width: (gif.images.original.width * this.canvasSize.height * 0.2) / gif.images.original.height,
				canvasHeight: this.canvasSize.height,
				canvasWidth: this.canvasSize.width,
			},
		]
	}

	@action
	scaleGifs() {
		for (let i = 0; i < this.gifs.length; i += 1) {
			const { height, width, canvasWidth, canvasHeight, x, y } = this.gifs[i]
			const scaleX = this.canvasSize.width / canvasWidth
			const scaleY = this.canvasSize.height / canvasHeight

			this.gifs[i] = {
				...this.gifs[i],
				height: height * scaleY,
				width: width * scaleX,
				x: x * scaleX,
				y: y * scaleY,
				canvasWidth: this.canvasSize.width,
				canvasHeight: this.canvasSize.height,
			}
		}
	}

	@action
	removeCut(cutIndex: number) {
		if (
			this.players[this.activePlayerIndex].playing &&
			(cutIndex === this.currentCutIndex || cutIndex === this.nextCutIndex)
		)
			return

		if (cutIndex === this.currentCutIndex) {
			playerService.changeModeToCreate(0)
			return
		}

		this.cuts = this.cuts
			.filter((item) => item.index !== cutIndex)
			.map((item, index) => {
				item.index = index
				return item
			})
	}

	@action
	removeSource(sourceIndex: number) {
		this.sources = this.sources
			.filter((item) => item.index !== sourceIndex)
			.map((item, index) => {
				item.index = index
				return item
			})
	}

	@action
	clearStore() {
		this.sources = []
		this.mode = Mode.Create
		this.cuts = []
		this.gifs = []
		this.players = [0, 1, 2, 3].map((player, i) => {
			return {
				id: i,
				playing: false,
				preloading: false,
				muted: false,
				url: this.sources[0] ? this.sources[0].url : '',
				isVisible: i === 0,
			}
		})
	}

	constructor() {
		makeObservable(this)
		autorun(() => console.log(`--> In ${this.mode.toUpperCase()} mode`))
	}
}

const store = new Store()
// window.store = store

export const trunk = new AsyncTrunk(store, { storageKey: 'eyeMoStore', storage: localStorage })

export default store
